<template>
  <div>
    <el-form-item label="宽度" prop="width">
      <el-input v-model="value.width" />
    </el-form-item>
    <el-form-item label="标签宽度" prop="isLabelWidth">
      <el-checkbox
        v-model="value.isLabelWidth"
        style="margin-right:5px;"
      >自定义</el-checkbox>
      <el-input-number
        v-model="value.labelWidth"
        :min="0"
        :max="200"
        :step="10"
        :disabled="!value.isLabelWidth"
      />
    </el-form-item>
    <el-form-item label="占位内容" prop="placeholder">
      <el-input v-model="value.placeholder" />
    </el-form-item>
    <el-form-item label="默认值" prop="defaultValue">
      <el-input v-model="value.defaultValue" />
    </el-form-item>
    <el-form-item label="自定义Class" prop="customClass">
      <el-input v-model="value.customClass" />
    </el-form-item>
    <el-form-item label="操作属性">
      <el-checkbox
        v-model="value.dataBind"
        disabled
      >数据绑定</el-checkbox>
      <el-checkbox
        v-model="value.hidden"
      >隐藏</el-checkbox>
      <el-checkbox
        v-model="value.disabled"
      >禁用</el-checkbox>
    </el-form-item>
    <el-form-item label="校验">
      <div>
        <el-checkbox
          v-model="value.required"
          @change="rule"
        >必填</el-checkbox>
      </div>
      <div>
        <el-input
          v-model="value.pattern"
          placeholder="填写正则表达式"
          size="mini"
          @input="rule"
        />
      </div>
    </el-form-item>
  </div>
</template>
<script>
export default {
  name: 'ETextarea',
  props: {
    value: {
      type: Object,
      default: () => {
        return {}
      }
    },
    rules: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data() {
    return {
    }
  },
  methods: {
    rule() {
      console.log(this.rules)
      this.rules.length = 0
      if (this.value.required) {
        this.rules.push({
          'required': true,
          'message': '多行文本必须填写'
        })
      }

      if (this.value.pattern) {
        this.rules.push({
          'pattern': this.value.pattern,
          'message': '多行文本格式不匹配'
        })
      }
    }
  }

}
</script>
