<template>
  <el-input-number
    v-model="itemData.options.defaultValue"
    :name="itemData.model"
    type="textarea"
    :placeholder="itemData.options.placeholder"
    :disabled="itemData.options.disabled"
    :class="itemData.options.customClass"
    :min="itemData.options.min"
    :max="itemData.options.max"
    :step="itemData.options.step"
    @change="change"
  />
</template>
<script>
export default {
  name: 'VNumber', // InputNumber 计数器
  props: {
    itemData: {
      type: Object,
      default: () => {
        return {
          'type': 'number', // 类型
          'options': {
            'width': '100%', // 宽度
            'defaultValue': '', // 默认值
            'required': false, // 验证
            'dataType': 'string', // 验证类型
            'placeholder': '', // 提示文字
            'customClass': '', // 自定义class
            'disabled': false, // 是否禁用
            'labelWidth': 100, // 标签宽度
            'isLabelWidth': false, // 是否启用标签宽度
            'hidden': false, // 是否隐藏
            'min': 0,
            'max': 0,
            'step': 1,
            'controlsPosition': ''
          },
          'name': '计数器',
          'key': '1576745527278',
          'model': 'input_1576745527278',
          'rules': []
        }
      }
    }
  },
  data() {
    return {
      data: '',
      name: ''
    }
  },
  created() {
    // this.data = this.itemData.options.defaultValue
    // this.name = this.itemData.model
    this.$emit('change', this.itemData.options.defaultValue, this.itemData.model) // 初始化form对象
  },
  methods: {
    change(val) {
      this.$emit('change', this.itemData.options.defaultValue, this.itemData.model) // 更新form对象
    }
  }
}
</script>
