      return false
    },
    cloneComponents: function(e) {
      const time = new Date().getTime()
      itemData = Object.assign({}, JSON.parse(JSON.stringify(e)), { key: time, model: `${e.type}_${time}`, choose: true })
      if (itemData.options && itemData.options.remoteOption !== undefined) {
        itemData.options.remoteOption = `option_${time}`
      }
      if (itemData.options && itemData.options.remoteFunc !== undefined) {
        itemData.options.remoteFunc = `func_${time}`
      }
      return itemData
    },

    itemClick(item) {
      this.itemData = item
      this.changeChoose(this.list)
    },
    nestAdd(e) {
      this.itemData = itemData
      this.changeChoose(this.list)
    },
    changeChoose(list) {
      for (let i = 0; i < list.length; i++) {
        // console.log(list[i].key, this.itemData.key)
        if (list[i].key === this.itemData.key) {
          list[i].choose = true
        } else {
          list[i].choose = false
        }
        if (list[i].type === 'grid') {
          list[i].columns.forEach((item, index) => {
            this.changeChoose(item.list)
          })
        }
        if (list[i].type === 'tabs') {
          list[i].tabs.forEach((item, index) => {
            this.changeChoose(item.list)
          })
        }
      }
    }
