<template>
  <el-radio-group
    v-model="itemData.options.defaultValue"
    :name="itemData.model"
    :disabled="itemData.options.disabled"
    @change="change"
  >
    <template v-if="itemData.options.button">
      <el-radio-button
        v-for="(item,index) in itemData.options.options"
        :key="index"
        :label="item.value"
        :style="style"
      >
        {{ item.label }}
      </el-radio-button>
    </template>
    <template v-else>
      <el-radio
        v-for="(item,index) in itemData.options.options"
        :key="index"
        :style="style"
        :label="item.value"
      >
        {{ item.label }}
      </el-radio>
    </template>
  </el-radio-group>
</template>
<script>
export default {
  name: 'VRadio',
  props: {
    itemData: {
      type: Object,
      default: () => {
        return {
          'type': 'radio', // 类型
          'options': {
            'button': false, // 是否使用button展示
            'inline': false, // 是否行内标签
            'defaultValue': '1', // 默认选中项
            'showLabel': false, // 是否显示标签
            'options': [ // 选项
              {
                'value': '1',
                'label': 'Option 1'
              },
              {
                'value': '2',
                'label': 'Option 2'
              },
              {
                'value': '3',
                'label': 'Option 3'
              }
            ],
            'required': false, // 是否必选
            'width': '', // 自定义宽度
            'props': {
              'value': 'value',
              'label': 'label'
            },
            'customClass': '', // 自定义class
            'labelWidth': 100, // label宽度
            'isLabelWidth': false, // 是否自定义label宽度
            'hidden': false, // 是否隐藏
            'disabled': false // 是否禁用
          },
          'name': '单选框组', // 标题
          'key': '1576663508195',
          'model': 'radio_1576663508195', // 字段标识
          'rules': [] // 验证规则
        }
      }
    }
  },
  data() {
    return {
      data: '',
      name: ''
    }
  }, // 单选框组
  computed: {
    style() {
      return {
        display: this.itemData.options.inline || this.itemData.options.button ? 'inline-block' : 'block',
        margin: this.itemData.options.inline && !this.itemData.options.button ? '4px 30px 4px 0' : '5px 0'
      }
    }
  },
  created() {
    // this.data = this.itemData.options.defaultValue
    // this.name = this.itemData.model
    this.$emit('change', this.itemData.options.defaultValue, this.itemData.model) // 初始化form对象
  },
  methods: {
    change(val) {
      this.$emit('change', this.itemData.options.defaultValue, this.itemData.model) // 更新form对象
    }
  }
}
</script>
