<template>
  <el-date-picker
    v-model="itemData.options.defaultValue"
    :name="itemData.model"
    :placeholder="itemData.options.placeholder"
    :disabled="itemData.options.disabled"
    :readonly="itemData.options.readonly"
    :editable="itemData.options.editable"
    :clearable="itemData.options.clearable"
    :class="itemData.options.customClass"
    :value-format="itemData.options.timestamp?'timestamp':itemData.options.format"
    :style="{'width':itemData.options.width}"
    :type="itemData.options.type"
    range-separator="-"
    :start-placeholder="itemData.options.startPlaceholder"
    :end-placeholder="itemData.options.endPlaceholder"
    :format="format"
    @change="change"
  />
</template>
<script>
export default {
  name: 'VDate', //
  props: {
    itemData: {
      type: Object,
      default: () => {
        return {
          'type': 'date', // 类型
          'icon': 'icon-time',
          'options': {
            'defaultValue': '', // 默认值
            'readonly': false,
            'disabled': false, // 是否禁用
            'editable': true, // 文本框可输入
            'clearable': true, // 是否显示清除按钮
            'placeholder': '', // 提示文字
            'startPlaceholder': '',
            'endPlaceholder': '',
            'required': false, // 验证
            'width': '', // 宽度
            'customClass': '', // 自定义class
            'labelWidth': 100, // 标签宽度
            'isLabelWidth': false, // 是否启用标签宽度
            'hidden': false, // 是否隐藏
            'dataBind': true,
            'remoteFunc': '',
            'remoteOption': '',
            'type': 'date',
            'format': 'yyyy-MM-dd',
            'timestamp': false
          },
          'name': '日期选择器',
          'key': '',
          'model': '',
          'rules': []
        }
      }
    }
  },
  data() {
    return {
      data: '',
      name: ''
    }
  },
  computed: {
    format: function() {
      if (this.itemData.options.type === 'week') {
        return 'yyyy 第 WW 周'
      }
      if (this.itemData.options.type === 'date') {
        return 'yyyy 年 MM 月 dd 日'
      }
      return 'yyyy-MM-dd'
    }
  },
  created() {
    // this.data = this.itemData.options.defaultValue
    // this.name = this.itemData.model
    this.$emit('change', this.itemData.options.defaultValue, this.itemData.model) // 初始化form对象
  },
  methods: {
    change(val) {
      this.$emit('change', this.itemData.options.defaultValue, this.itemData.model) // 更新form对象
    }
  }
}
</script>
