<template>
  <div>
    <el-form-item label="宽度" prop="width">
      <el-input v-model="value.width" />
    </el-form-item>
    <el-form-item label="标签宽度" prop="isLabelWidth">
      <el-checkbox
        v-model="value.isLabelWidth"
        style="margin-right:5px;"
      >自定义</el-checkbox>
      <el-input-number
        v-model="value.labelWidth"
        :min="0"
        :max="200"
        :step="10"
        :disabled="!value.isLabelWidth"
      />
    </el-form-item>
    <el-form-item label="占位内容" prop="placeholder">
      <el-input v-model="value.placeholder" />
    </el-form-item>
    <el-form-item label="默认值" prop="defaultValue">
      <el-input v-model="value.defaultValue" />
    </el-form-item>
    <el-form-item label="自定义Class" prop="customClass">
      <el-input v-model="value.customClass" />
    </el-form-item>
    <el-form-item label="操作属性">
      <el-checkbox
        v-model="value.dataBind"
        disabled
      >数据绑定</el-checkbox>
      <el-checkbox
        v-model="value.hidden"
      >隐藏</el-checkbox>
      <el-checkbox
        v-model="value.disabled"
      >禁用</el-checkbox>
      <el-checkbox
        v-model="value.showPassword"
      >显示密码</el-checkbox>
    </el-form-item>
    <el-form-item label="校验">
      <div>
        <el-checkbox
          v-model="value.required"
          @change="rule"
        >必填</el-checkbox>
      </div>
      <el-select
        v-model="value.dataType"
        placeholder="请选择"
        size="mini"
        @change="rule"
      >
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        />
      </el-select>
      <div>
        <el-input
          v-model="value.pattern"
          placeholder="填写正则表达式"
          size="mini"
          @input="rule"
        />
      </div>
    </el-form-item>
  </div>
</template>
<script>
export default {
  name: 'EInput',
  props: {
    value: {
      type: Object,
      default: () => {
        return {}
      }
    },
    rules: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data() {
    return {
      options: [
        {
          label: '字符串',
          value: 'string'
        },
        {
          label: '数字',
          value: 'number'
        },
        {
          label: '布尔值',
          value: 'boolean'
        },
        {
          label: '整数',
          value: 'integer'
        },
        {
          label: '浮点数',
          value: 'float'
        },
        {
          label: 'URL地址',
          value: 'url'
        },
        {
          label: '邮箱地址',
          value: 'email'
        },
        {
          label: '十六进制',
          value: 'hex'
        }
      ]
    }
  },
  methods: {
    rule() {
      console.log(this.rules)
      this.rules.length = 0
      if (this.value.required) {
        this.rules.push({
          'required': true,
          'message': '单行文本必须填写'
        })
      }
      if (this.value.dataType) {
        this.rules.push({
          'type': this.value.dataType,
          'message': '单行文本格式不正确'
        })
      }
      if (this.value.pattern) {
        this.rules.push({
          'pattern': this.value.pattern,
          'message': '单行文本格式不匹配'
        })
      }
    }
  }

}
</script>
