<template>
  <el-form-item
    :label="`${item.name}:`"
    :prop="item.model"
    :label-width="item.options&&item.options.isLabelWidth?item.options.labelWidth+'px':''"
  >
    <!-- 单选框 -->
    <v-radio v-if="item.type=='radio'" :item-data="item" @change="change" />
    <!-- 多选框 -->
    <v-checkbox v-if="item.type=='checkbox'" :item-data="item" @change="change" />
    <!-- 单行文本 -->
    <v-input v-if="item.type=='input'" :item-data="item" @change="change" />
    <!-- 多行文本 -->
    <v-textarea v-if="item.type=='textarea'" :item-data="item" @change="change" />
    <!-- 计数器 -->
    <v-number v-if="item.type=='number'" :item-data="item" @change="change" />
    <!-- 时间选择器 -->
    <v-time v-if="item.type=='time'" :item-data="item" @change="change" />
    <!-- 日期选择器 -->
    <v-date v-if="item.type=='date'" :item-data="item" @change="change" />
    <!-- 下拉选择框 -->
    <v-select v-if="item.type=='select'" :item-data="item" @change="change" />
    <!-- 文字 -->
    <v-text v-if="item.type=='text'" :item-data="item" @change="change" />
    <!-- HTML -->
    <v-html v-if="item.type=='html'" :item-data="item" @change="change" />

    <slot name="viewEdit" />
  </el-form-item>
</template>
<script>
import VRadio from './Radio'
import VCheckbox from './Checkbox'
import VInput from './Input'
import VTextarea from './Textarea'
import VNumber from './Number'
import VTime from './Time'
import VDate from './Date'
import VSelect from './Select'
import VText from './Text'
import VHtml from './Html'
export default {
  name: 'Item',
  components: { VRadio, VCheckbox, VInput, VTextarea, VNumber, VTime, VDate, VSelect, VText, VHtml },
  props: {
    item: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  created() {
  },
  methods: {
    change(val, name) {
      this.$emit('change', val, name) // 事件传递
    }
  }
}
</script>
