<template>
  <el-form
    ref="form"
    :model="value"
    :label-width="data.config && data.config.labelWidth+'px'"
    :size="data.config && data.config.size"
    :label-position="data.config && data.config.labelPosition"
    :class="data.config && data.config.customClass"
  >
    <template v-for="(item,index) in data.list">
      <!-- 栅格布局 -->
      <grid v-if="item.type=='grid'" :key="index" :item-data="item" :model="value" @change="change" />
      <!-- tabs -->
      <tabs v-else-if="item.type=='tabs'" :key="index" :item-data="item" :model="value" @change="change" />
      <!-- divider -->
      <v-divider v-else-if="item.type=='divider'" :key="index" :item-data="item" :model="value" @change="change" />
      <item v-else :key="index" :item="item" :model="value" @change="change" />
    </template>

  </el-form>
</template>

<script>
import Item from './form/Item'
import Grid from './form/Grid'
import Tabs from './form/Tabs'
import VDivider from './form/Divider'

export default {
  name: 'DanForm',
  components: { Item, Grid, VDivider, Tabs },
  props: {
    data: {
      type: Object,
      default: () => {
        return {
          list: [],
          config: {
            'labelWidth': 100,
            'labelPosition': 'right',
            'size': 'small',
            'customClass': ''
          }
        }
      }
    },
    remoteFuncs: {
      type: Object,
      default: () => {
        return {}
      }
    }, editData: {
      type: Object,
      default: () => {
        return {}
      }
    }, dynamicData: {
      type: Object,
      default: () => {
        return {}
      }
    },
    value: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data: function() {
    return {
      form: {}
    }
  },
  watch: {
    value: function(newVal, oldVal) {
      if (JSON.stringify(oldVal) !== JSON.stringify(newVal)) {
        // this.initVal(this.data.list)
      }
    }
  },
  created() {
    // 初始化value
    // console.log(this.value)
    this.initVal(this.data.list)
  },
  methods: {
    reset() {
      this.$refs.form.$el.reset()
    },
    change(val, name) {
      this.form[name] = val // 修改form值
      this.$emit('input', JSON.parse(JSON.stringify(this.form))) // 提交修改
    },
    initVal(list) {
      for (let i = 0; i < list.length; i++) {
        if (this.value[list[i].model] !== undefined && list[i].type !== 'grid') {
          list[i].options.defaultValue = this.value[list[i].model]
        }
        if (list[i].type === 'grid') {
          list[i].columns.forEach((item, index) => {
            this.initVal(item.list)
          })
        }
      }
    }
  }

}
</script>

