<template>
  <div>
    <el-form-item label="宽度" prop="width">
      <el-input v-model="value.width" />
    </el-form-item>
    <el-form-item label="标签宽度" prop="isLabelWidth">
      <el-checkbox
        v-model="value.isLabelWidth"
        style="margin-right:5px;"
      >自定义</el-checkbox>
      <el-input-number
        v-model="value.labelWidth"
        :min="0"
        :max="200"
        :step="10"
        :disabled="!value.isLabelWidth"
      />
    </el-form-item>

    <el-form-item label="布局方式" prop="inline">
      <el-radio-group v-model="value.inline">
        <el-radio-button :label="false">块级</el-radio-button>
        <el-radio-button :label="true">行内</el-radio-button>
      </el-radio-group>
    </el-form-item>

    <el-form-item label="是否显示标签" prop="showLabel">
      <el-switch
        v-model="value.showLabel"
        @change="showLabel"
      />
    </el-form-item>

    <el-form-item label="选项">
      <el-radio-group v-model="value.remote">
        <el-radio-button :label="false">静态数据</el-radio-button>
        <el-radio-button :label="true">动态数据</el-radio-button>
      </el-radio-group>
      <template v-if="!value.remote">
        <el-radio-group v-model="value.defaultValue">
          <draggable
            ghost-class="ghost"
            tag="ul"
            handle=".drag-item"
            :list="value.options"
            @start="dragging = true"
            @end="dragging = false;"
            @sort="sort"
          >
            <li v-for="(item,index) in value.options" :key="index" style="margin:5px 0;">
              <el-radio style="margin-right:4px;" :label="item.value">
                <el-input v-if="value.showLabel" v-model="item.value" size="mini" style="width:90px" />
                <el-input v-else v-model="item.value" size="mini" style="width:180px" @input="changeVal(item.value,item)" />
                <el-input v-if="value.showLabel" v-model="item.label" size="mini" style="width:90px" />
              </el-radio>
              <i class="drag-item" style="font-size: 16px; margin: 0px 5px; cursor: move;"><i class="iconfont icon-icon_bars" /></i>
              <el-button
                type="danger"
                icon="el-icon-minus"
                circle
                size="mini"
                plain
                style="padding: 4px; margin-left: 5px;"
                @click="remove(index,value.options)"
              />
            </li>
          </draggable>

        </el-radio-group>

        <div style="margin-left: 22px;">
          <el-button type="text" size="small" @click="addOption">添加选项</el-button>
        </div>
      </template>
      <template v-else>
        <el-radio-group v-model="value.remoteType">
          <el-radio label="option">赋值变量</el-radio>
          <el-radio label="func">方法函数</el-radio>
        </el-radio-group>
        <el-input v-show="value.remoteType=='option'" v-model="value.remoteOption" size="mini" />
        <el-input v-show="value.remoteType=='func'" v-model="value.remoteFunc" size="mini" />
        <el-input v-model="value.props.value" placeholder="" style="margin:5px 0;">
          <template slot="prepend"><div style="width:50px">值</div></template>
        </el-input>
        <el-input v-model="value.props.label" placeholder="" style="margin:5px 0;">
          <template slot="prepend"><div style="width:50px">标签</div></template>
        </el-input>
      </template>

    </el-form-item>

    <el-form-item label="自定义Class" prop="customClass">
      <el-input v-model="value.customClass" />
    </el-form-item>
    <el-form-item label="操作属性">
      <el-checkbox
        v-model="value.dataBind"
        disabled
      >数据绑定</el-checkbox>
      <el-checkbox
        v-model="value.hidden"
      >隐藏</el-checkbox>
      <el-checkbox
        v-model="value.disabled"
      >禁用</el-checkbox>

    </el-form-item>
    <el-form-item label="校验">
      <div>
        <el-checkbox
          v-model="value.required"
          @change="rule"
        >必填</el-checkbox>
      </div>

    </el-form-item>
  </div>
</template>
<script>
import draggable from 'vuedraggable'
export default {
  name: 'ERadio',
  components: { draggable },
  props: {
    value: {
      type: Object,
      default: () => {
        return {}
      }
    },
    rules: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data() {
    return {
    }
  },
  created() {

  },
  methods: {
    remove(index, list) {
      list.splice(index, 1)
    },
    rule() {
      console.log(this.rules)
      this.rules.length = 0
      if (this.value.required) {
        this.rules.push({
          'required': true,
          'message': '单选框组必须填写'
        })
      }
    },
    showLabel(val) {
      console.log(val)
      if (!val) {
        this.value.options.map(item => {
          item.label = item.value
          return item
        })
      }
    },
    changeVal(val, item) {
      item.label = val
    },
    addOption() {
      const length = this.value.options.length + 1
      this.value.options.push({
        label: '新选项' + length,
        value: '新选项' + length
      })
    },
    sort() {
      console.log(this.options)
    }
  }

}
</script>
