<template>
  <el-select
    v-model="itemData.options.defaultValue"
    :name="itemData.model"
    :disabled="itemData.options.disabled"
    :clearable="itemData.options.clearable"
    :multiple="itemData.options.multiple"
    :placeholder="itemData.options.placeholder"
    :filterable="itemData.options.filterable"
    :style="{'width':itemData.options.width}"
    :class="itemData.options.customClass"
    @change="change"
  >
    <el-option
      v-for="(item,index) in itemData.options.options"
      :key="index"
      :label="item.label"
      :value="item.value"
    />
  </el-select>
</template>
<script>
export default {
  name: 'VSelect',
  props: {
    itemData: {
      type: Object,
      default: () => {
        return {
          'type': 'select', // 类型
          'options': {
            'defaultValue': '1', // 默认选中项
            'showLabel': false, // 是否显示标签
            'multiple': false, // 多选
            'clearable': false,
            'placeholder': '',
            'options': [ // 选项
              {
                'value': '1',
                'label': 'Option 1'
              },
              {
                'value': '2',
                'label': 'Option 2'
              },
              {
                'value': '3',
                'label': 'Option 3'
              }
            ],
            'required': false, // 是否必选
            'width': '', // 自定义宽度
            'remote': false,
            'remoteType': 'option',
            'remoteOption': 'option_1578021388567',
            'filterable': false, // 是否可搜索
            'remoteOptions': [],
            'remoteFunc': 'func_1578021388567',
            'props': {
              'value': 'value',
              'label': 'label'
            },
            'customClass': '', // 自定义class
            'labelWidth': 100, // label宽度
            'isLabelWidth': false, // 是否自定义label宽度
            'hidden': false, // 是否隐藏
            'disabled': false // 是否禁用
          },
          'name': '单选框组', // 标题
          'key': '',
          'model': '', // 字段标识
          'rules': [] // 验证规则
        }
      }
    }
  },
  data() {
    return {
      data: '',
      name: ''
    }
  }, // 单选框组
  computed: {
  },
  created() {
    // this.data = this.itemData.options.defaultValue
    // this.name = this.itemData.model
    this.$emit('change', this.itemData.options.defaultValue, this.itemData.model) // 初始化form对象
  },
  methods: {
    change(val) {
      this.$emit('change', this.itemData.options.defaultValue, this.itemData.model) // 更新form对象
    }
  }
}
</script>
