<template>
  <div>
    <el-form-item label="宽度" prop="width">
      <el-input v-model="value.width" />
    </el-form-item>
    <el-form-item label="标签宽度" prop="isLabelWidth">
      <el-checkbox
        v-model="value.isLabelWidth"
        style="margin-right:5px;"
      >自定义</el-checkbox>
      <el-input-number
        v-model="value.labelWidth"
        :min="0"
        :max="200"
        :step="10"
        :disabled="!value.isLabelWidth"
      />
    </el-form-item>
    <el-form-item label="显示类型" prop="type">
      <el-select
        v-model="value.type"
        placeholder="请选择"
        size="mini"
        @change="rangeChange"
      >
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        />
      </el-select>
    </el-form-item>
    <el-form-item label="是否获取时间戳" prop="timestamp">
      <el-switch
        v-model="value.timestamp"
      />
    </el-form-item>
    <el-form-item v-if="value.type.indexOf('range')=='-1'" label="占位内容" prop="placeholder">
      <el-input v-model="value.placeholder" />
    </el-form-item>
    <el-form-item v-if="value.type.indexOf('range')!=-1" label="开始时间占位内容" prop="startPlaceholder">
      <el-input v-model="value.startPlaceholder" />
    </el-form-item>
    <el-form-item v-if="value.type.indexOf('range')!=-1" label="结束时间占位内容" prop="endPlaceholder">
      <el-input v-model="value.endPlaceholder" />
    </el-form-item>
    <el-form-item label="格式" prop="format">
      <el-input v-model="value.format" />
    </el-form-item>

    <el-form-item label="默认值" prop="defaultValue">
      <el-date-picker
        v-model="value.defaultValue"
        range-separator="-"
        :type="value.type"
        style="width:100%"
        :format="format"
        :value-format="value.timestamp?'timestamp':value.format"
      />
    </el-form-item>

    <el-form-item label="自定义Class" prop="customClass">
      <el-input v-model="value.customClass" />
    </el-form-item>
    <el-form-item label="操作属性">
      <el-checkbox
        v-model="value.dataBind"
        disabled
      >数据绑定</el-checkbox>
      <el-checkbox
        v-model="value.hidden"
      >隐藏</el-checkbox>
      <el-checkbox
        v-model="value.readonly"
      >完全只读</el-checkbox>
      <el-checkbox
        v-model="value.disabled"
      >禁用</el-checkbox>
      <el-checkbox
        v-model="value.editable"
      >文本框可输入</el-checkbox>
      <el-checkbox
        v-model="value.clearable"
      >显示清除按钮</el-checkbox>

    </el-form-item>
    <el-form-item label="校验">
      <div>
        <el-checkbox
          v-model="value.required"
          @change="rule"
        >必填</el-checkbox>
      </div>
    </el-form-item>
  </div>
</template>
<script>
export default {
  name: 'EDate',
  props: {
    value: {
      type: Object,
      default: () => {
        return {}
      }
    },
    rules: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data() {
    return {
      options: [
        { label: 'year', value: 'year' },
        { label: 'month', value: 'month' },
        { label: 'date', value: 'date' },
        { label: 'dates', value: 'dates' },
        { label: 'week', value: 'week' },
        { label: 'datetime', value: 'datetime' },
        { label: 'datetimerange', value: 'datetimerange' },
        { label: 'daterange', value: 'daterange' },
        { label: 'monthrange', value: 'monthrange' }
      ]
    }
  },
  computed: {
    format: function() {
      if (this.value.type === 'week') {
        return 'yyyy 第 WW 周'
      }
      if (this.value.type === 'date') {
        return 'yyyy 年 MM 月 dd 日'
      }
      return 'yyyy-MM-dd'
    }
  },
  methods: {
    rule() {
      console.log(this.rules)
      this.rules.length = 0
      if (this.value.required) {
        this.rules.push({
          'required': true,
          'message': '日期必须填写'
        })
      }
    },
    rangeChange() {
      this.value.defaultValue = ''
    }
  }

}
</script>
