<template>
  <div>
    <!-- 官方文档中使用 id，这里禁止使用，在后期打包后容易出现问题，使用 ref 或者 DOM 就行 -->
    <div ref="ace" class="ace-editor" />
  </div>
</template>
<script>
import ace from 'ace-builds'
import 'ace-builds/webpack-resolver' // 在 webpack 环境中使用必须要导入
// import 'ace-builds/src-noconflict/theme-monokai' // 默认设置的主题
import 'ace-builds/src-noconflict/mode-javascript' // 默认设置的语言模式
import 'ace-builds/src-noconflict/mode-html'
import 'ace-builds/src-noconflict/mode-css'
import 'ace-builds/src-noconflict/mode-scss'
import 'ace-builds/src-noconflict/mode-json'
import 'ace-builds/src-noconflict/mode-java'
import 'ace-builds/src-noconflict/mode-text'

import 'ace-builds/src-noconflict/snippets/javascript'
import 'ace-builds/src-noconflict/snippets/html'
import 'ace-builds/src-noconflict/snippets/css'
import 'ace-builds/src-noconflict/snippets/scss'
import 'ace-builds/src-noconflict/snippets/json'
import 'ace-builds/src-noconflict/snippets/java'
import 'ace-builds/src-noconflict/snippets/text'

import 'ace-builds/src-noconflict/ext-beautify'
import 'ace-builds/src-noconflict/ext-language_tools'
export default {
  name: 'Ace',
  props: {
    value: {
      type: String,
      default: ''
    },
    themePath: {
      type: String,
      default: 'ace/theme/monokai'
    },
    modePath: {
      type: String,
      default: 'ace/mode/javascript'
    },
    readOnly: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      aceEditor: null
    }
  },
  watch: {
    value(val) {
      console.log(val)
      this.aceEditor.updateText()
    },
    readOnly(val) {
      console.log(val)
      this.aceEditor.setReadOnly(val)
    }
  },
  mounted() {
    this.aceEditor = ace.edit(this.$refs.ace, {
      value: this.value ? this.value : '',
      readOnly: this.readOnly, // 设置只读
      maxLines: 20, // 最大行数，超过会自动出现滚动条
      minLines: 10, // 最小行数，还未到最大行数时，编辑器会自动伸缩大小
      fontSize: 14, // 编辑器内字体大小
      // theme: this.themePath, // 默认设置的主题
      mode: this.modePath, // 默认设置的语言模式
      tabSize: 4 // 制表符设置为 4 个空格大小
    })
    this.aceEditor.setOptions({
      enableSnippets: true,
      enableLiveAutocompletion: true,
      enableBasicAutocompletion: true
    })
    this.aceEditor.getSession().on('change', this.change)
  },
  methods: {
    change() {
      this.$emit('input', this.aceEditor.getSession().getValue())
    }
  }
}
</script>
