<template>
  <draggable
    :list="list"
    :class="[isChild?'widget-col-list':'widget-form-list']"
    ghost-class="ghost"
    :move="checkMove"
    tag="div"
    group="components"
    :animation="200"
    :disabled="false"
    handle=".drag-widget"
    @start="dragging = true"
    @end="dragging = false;"
    @add="add"
  >
    <div
      v-for="(item,index) in list"
      :key="index"
      @click.stop="$emit('itemClick',item)"
    >

      <div
        v-if="item.type=='divider'"
        class="widget-view widget-item"
        :class="[item.options.customClass,item.choose?'active':'']"
      >
        <el-divider :content-position="item.options.contentPosition">{{ item.name }}</el-divider>
        <div v-if="item.choose" class="widget-view-action"><i class="iconfont icon-icon_clone" @click.stop="cloneItem(item,index,list)" /><i class="iconfont icon-trash" @click.stop="trashItem(item,index,list)" /></div>
        <div v-if="item.choose" class="widget-view-drag"><i class="iconfont icon-drag drag-widget" /></div>
      </div>
      <el-row
        v-else-if="item.type=='grid'"
        :key="index"
        :gutter="item.options.gutter"
        :justify="item.options.justify"
        :align="item.options.align"
        :class="[item.options.customClass,item.choose?'active':'']"
        class="widget-view widget-col"
      >
        <el-col v-for="(item2,index2) in item.columns" :key="index2" :span="item2.span">
          <nested-draggable
            v-if="item2.list"
            :list="item2.list"
            :is-child="true"
            @itemClick="itemClick"
            @add="add"
          />
        </el-col>
        <div v-if="item.choose" class="widget-view-action"><i class="iconfont icon-icon_clone" @click.stop="cloneItem(item,index,list)" /><i class="iconfont icon-trash" @click.stop="trashItem(item,index,list)" /></div>
        <div v-if="item.choose" class="widget-view-drag"><i class="iconfont icon-drag drag-widget" /></div>
      </el-row>
      <div
        v-else-if="item.type=='tabs'"
        :key="index"
        :class="[item.options.customClass,item.choose?'active':'']"
        class="widget-view widget-col"
      >
        <el-tabs
          v-model="item.options.defaultValue"
          :type="item.options.type"
          :tab-position="item.options.tabPosition"
        >
          <el-tab-pane v-for="(item2,index2) in item.tabs" :key="index2" :label="item2.label" :name="item2.name">
            <nested-draggable
              v-if="item2.list"
              :list="item2.list"
              :is-child="true"
              @itemClick="itemClick"
              @add="add"
            />
          </el-tab-pane>
        </el-tabs>
        <div v-if="item.choose" class="widget-view-action"><i class="iconfont icon-icon_clone" @click.stop="cloneItem(item,index,list)" /><i class="iconfont icon-trash" @click.stop="trashItem(item,index,list)" /></div>
        <div v-if="item.choose" class="widget-view-drag"><i class="iconfont icon-drag drag-widget" /></div>
      </div>

      <item
        v-else
        :key="index"
        :item="item"
        class="widget-view widget-item"
        :class="{active:item.choose}"
        @change="change"
      >
        <template v-slot:viewEdit>
          <div class="widget-view-model">{{ item.model }}</div>
          <div v-if="item.choose" class="widget-view-action"><i class="iconfont icon-icon_clone" @click.stop="cloneItem(item,index,list)" /><i v-if="list.length > 1" class="iconfont icon-trash" @click.stop="trashItem(item,index,list)" /></div>
          <div v-if="item.choose" class="widget-view-drag"><i class="iconfont icon-drag drag-widget" /></div>
        </template>
      </item>
    </div>
  </draggable>
</template>

<script>
import draggable from 'vuedraggable'
import Item from '../../packages/DanForm/form/Item'
export default {
  name: 'NestedDraggable',
  components: {
    draggable, Item
  },
  props: {
    isChild: {
      type: Boolean,
      default: false
    },
    list: {
      required: true,
      type: Array
    }
  },
  data() {
    return {
      dragging: false
    }
  },
  methods: {
    add(e) {
      // console.log(e)
      this.$emit('add', e)
    },
    checkMove: function(e) {
      // window.console.log('Future index: ' + e.draggedContext.futureIndex)
    },
    change(val, name) {
      // console.log(val, name)
    },
    itemClick(item) {
      this.$emit('itemClick', item)
    },
    changeItem(list) {
      for (let i = 0; i < list.length; i++) {
        const time = Math.random().toString(36).slice(-8)
        list[i].key = time
        list[i].model = `${list[i].type}_${time}`
        if (list[i].type === 'grid') {
          list[i].columns.forEach((item, index) => {
            this.changeItem(item.list)
          })
        }
      }
    },
    cloneItem: function(item, index, list) {
      // item.choose = false
      const time = new Date().getTime()
      const itemData = Object.assign({}, JSON.parse(JSON.stringify(item)), { key: time, model: `${item.type}_${time}` })
      if (itemData.type === 'grid') {
        itemData.columns.forEach((item, index) => {
          this.changeItem(item.list)
        })
      }
      list.splice(index + 1, 0, itemData)
      this.$emit('itemClick', itemData)
    },
    trashItem: function(item, index, list) {
      list.splice(index, 1)
      this.$emit('itemClick', list[index - 1 < 0 ? 0 : index - 1])
    }
  }
}
</script>
