<template>
  <div>
    <el-form-item label="宽度" prop="width">
      <el-input v-model="value.width" />
    </el-form-item>
    <el-form-item label="标签宽度" prop="isLabelWidth">
      <el-checkbox
        v-model="value.isLabelWidth"
        style="margin-right:5px;"
      >自定义</el-checkbox>
      <el-input-number
        v-model="value.labelWidth"
        :min="0"
        :max="200"
        :step="10"
        :disabled="!value.isLabelWidth"
      />
    </el-form-item>
    <el-form-item label="是否为范围选择" prop="isRange">
      <el-switch
        v-model="value.isRange"
        @change="rangeChange"
      />
    </el-form-item>
    <el-form-item v-if="!value.isRange" label="占位内容" prop="placeholder">
      <el-input v-model="value.placeholder" />
    </el-form-item>
    <el-form-item v-if="value.isRange" label="开始时间占位内容" prop="startPlaceholder">
      <el-input v-model="value.startPlaceholder" />
    </el-form-item>
    <el-form-item v-if="value.isRange" label="结束时间占位内容" prop="endPlaceholder">
      <el-input v-model="value.endPlaceholder" />
    </el-form-item>
    <el-form-item label="格式" prop="format">
      <el-input v-model="value.format" />
    </el-form-item>

    <el-form-item v-if="!value.isRange" label="默认值" prop="defaultValue">
      <el-time-select
        v-model="value.defaultValue"
        :picker-options="{
          start: '00:00',
          step: '00:15',
          end: '23:59'
        }"
      />
    </el-form-item>

    <el-form-item v-if="value.isRange" label="默认值" prop="defaultValue">
      <el-time-picker
        v-model="value.defaultValue"
        is-range
        range-separator="-"
      />
    </el-form-item>

    <el-form-item label="自定义Class" prop="customClass">
      <el-input v-model="value.customClass" />
    </el-form-item>
    <el-form-item label="操作属性">
      <el-checkbox
        v-model="value.dataBind"
        disabled
      >数据绑定</el-checkbox>
      <el-checkbox
        v-model="value.hidden"
      >隐藏</el-checkbox>
      <el-checkbox
        v-model="value.readonly"
      >完全只读</el-checkbox>
      <el-checkbox
        v-model="value.disabled"
      >禁用</el-checkbox>
      <el-checkbox
        v-model="value.editable"
      >文本框可输入</el-checkbox>
      <el-checkbox
        v-model="value.clearable"
      >显示清除按钮</el-checkbox>
      <el-checkbox
        v-model="value.arrowControl"
      >使用箭头进行时间选择</el-checkbox>
    </el-form-item>
    <el-form-item label="校验">
      <div>
        <el-checkbox
          v-model="value.required"
          @change="rule"
        >必填</el-checkbox>
      </div>
    </el-form-item>
  </div>
</template>
<script>
export default {
  name: 'ETime',
  props: {
    value: {
      type: Object,
      default: () => {
        return {}
      }
    },
    rules: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data() {
    return {
    }
  },
  methods: {
    rule() {
      console.log(this.rules)
      this.rules.length = 0
      if (this.value.required) {
        this.rules.push({
          'required': true,
          'message': '时间必须填写'
        })
      }
    },
    rangeChange() {
      this.value.defaultValue = ''
    }
  }

}
</script>
